<template>
	<div id="app"><router-view /></div>
</template>

<script>
export default {
	name: 'App',
	methods: {
		sendMessage(cmd, params) {
			window.parent.postMessage({ cmd, params }, '*');
		},
		handleMessage(msg) {
			const { data } = msg;
			if (data.cmd === 'preset') { // 预设的企业信息
				this.$store.commit('setUser', {
					entName: data.params['company_title'], //企业名称
					entCode: data.params['tax_id'], //企业统一社会信用代码
					legalpersonName: data.params['legal_person'], //法定代表人（负责人）姓名
					legalpersonIdNum: data.params['legal_person_id'], //法定代表人（负责人）证件号码
					entPersonName: data.params['legal_person'], //工商联络员姓名
					entPersonIdType: '中华人民共和国居民身份证', //工商联络员证件类型
					entPersonIdNum: data.params['legal_person_id'], //工商联络员证件号码
					entPersonTel: data.params['legal_person_tel'] //工商联络员手机号
				})
			}
			if (data.cmd === 'getAnswer') {
				const answer = this.$store.state.answer
				let keys = Object.keys(answer)
				if (keys.length > 0) {
					this.sendMessage('echo', {business_id: data.params['business_id'], answer: answer})
					this.$store.commit('resetAnswer')
				}
			}
		}
	},
	mounted() {
		this.$nextTick(() => {
			console.log('工商年报系统注册消息成功')
			window.addEventListener('message', this.handleMessage);
		});
	},
	destroyed() {
		window.removeEventListener('message', this.handleMessage);
	}
};
</script>
